*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}body{display:flex;flex-direction:column;width:500px;height:80vh;margin:10vh auto;border:2px solid black}header{display:flex;justify-content:space-between;align-items:center;font-size:20px;padding:0 5px 0 5px}header #setting,header #add{cursor:pointer}main{flex:1;display:flex;flex-direction:column-reverse;overflow-y:scroll;border-bottom:2px solid black;border-top:2px solid black}main::-webkit-scrollbar{display:none}footer{display:flex;justify-content:space-between;align-items:center;height:30px;width:100%;padding:0 10px 0 10px}footer .input{flex:1;height:100%;margin-right:10px}footer .input #input{outline:none;font-size:16px;border:none;width:100%;height:100%}footer .input #input:focus{outline:none}footer #send{cursor:pointer}.chatbox{position:relative;width:350px;border-radius:16px;padding:5px;margin-bottom:10px}.chatbox .chatname{font-size:11px;margin-left:5px;margin-bottom:3px}.chatbox .chattext{width:290px;word-wrap:break-word;border:2px solid black;border-radius:10px;padding:3px 3px}.chatbox .chattime{position:absolute;width:49px;right:5px;bottom:5px;font-size:11px;text-align:start}.chatbox-right{align-self:flex-end}.chatbox-right .chatname-right{margin-left:55px}.chatbox-right .chattext-right{float:right}.chatbox-right .chattime-right{left:5px;text-align:end}
